* {

    margin: 0;
    
    padding: 0;
    
    box-sizing: border-box;
    
    }
    
    body {
    
    font-family: cursive;
    
    }
    
    a {
    
    text-decoration: none;
    
    }

    
    li {
    
    list-style: none;
    
    }
  
    .bg{
      z-index: -1;
    }
  
    .navbar {
  
      display: flex;
      
      align-items: center;
      
      justify-content: space-between;
      
      padding: 20px;
      
      background: linear-gradient(90deg, black, #11119d);
      
      color: #fff;
      
      }
      
      .nav-links a {
      
      color: #fff;
      
      }

      .nav-links a:hover{
          color:black;
      }
  
      .logo{
        height: 5rem;
        width: 5rem;
      }
  
        
  .menu {
  
    display: flex;
    
    gap: 1em;
    
    font-size: 18px;
    
    }
    
    .menu li:hover {
    
    background-color: #4c9e9e;
    
    border-radius: 5px;
    
    transition: 0.3s ease;

    
    }
    
    .menu li {
    
    padding: 5px 14px;
    
    }
  
    .services {
  
      position: relative; 
      
      }
      
      .dropdown {
      
      background-color: rgb(1, 139, 139);
      
      padding: 1em 0;
      
      position: absolute; /*WITH RESPECT TO PARENT*/
      
      display: none;
      
      border-radius: 8px;
      
      top: 35px;
      
      }
      
      .dropdown li + li {
      
      margin-top: 10px;
      
      }
  
      .dropdown li {
  
        padding: 0.5em 1em;
        
        width: 8em;
        
        text-align: center;
        
        }
        
        .dropdown li:hover {
        
        background-color: #4c9e9e;
        
        }
        
        .services:hover .dropdown {
        
        display: block;
        
        }

